/* =====================
Nav
===================== */
/* This class is added on scroll */
header{
    display: block;
    position: fixed;
    top: -4em;
    left: 0;
    right: 0;
    width: inherit;
    max-width: inherit;
    height: 4em;
    margin: 0 auto;
    transition: top 400ms ease-in-out;
    z-index: 5;
}
header.fixed{
    top: 0;
    transition: top 400ms ease-in-out;
}
nav{
    display: block;
    position: relative;
    width: 100%;
    height: 2.25em;
    margin: -1.5em auto 0 auto;

    /* background: color($color-black a(50%)); */
}
nav ul{
    display: table;
    height: 100%;
    width: auto;
    margin: 0 auto;
    padding: 0 8px;
    padding: 0 .5rem;
}
nav li{
    display: table-cell;
    height: inherit;
    padding: 0;
    vertical-align: middle;
}
nav li.to--pitch{
    display: none;
    position: absolute;
    left: 0;
}
header.stuck nav li.to--pitch{
    display: table-cell;
}
nav li a{
    background-color: hsl(232,1%,18%);
}
nav li a, nav li a:hover, nav li a:visited, nav li a:active{
    outline: 0;
    text-decoration: none;
}
header.stuck nav li a{
    background-color: #323433;
    background-color: rgba(50, 52, 51, 0.3);
    transition: background-color 300ms ease-out;
}
nav li:not(.to--pitch) a{
    display: block;
    height: 3em;
    width: 3em;
    margin: 0 .25em;
    border-radius: 0 100% 100% 100%;
    -webkit-transform: rotate(-90deg);
                -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
    transition: background-color 300ms ease-out, -webkit-transform 550ms ease-out;
    transition: background-color 300ms ease-out, transform 550ms ease-out;
    transition: background-color 300ms ease-out, transform 550ms ease-out, -webkit-transform 550ms ease-out;
}
nav li:not(.to--pitch) a:hover{
    outline: 0;
    -webkit-transform: rotate(-135deg);
                -ms-transform: rotate(-135deg);
            transform: rotate(-135deg);
    transition: background-color 300ms ease-in, -webkit-transform 300ms ease-in;
    transition: background-color 300ms ease-in, transform 300ms ease-in;
    transition: background-color 300ms ease-in, transform 300ms ease-in, -webkit-transform 300ms ease-in;
}
nav li:not(.to--pitch) a.active{
    outline: 0;
    -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    transition: background-color 300ms ease-in, -webkit-transform 400ms ease-in;
    transition: background-color 300ms ease-in, transform 400ms ease-in;
    transition: background-color 300ms ease-in, transform 400ms ease-in, -webkit-transform 400ms ease-in;
}
nav li.to--pitch a{
    display: block;
    height: 24px;
    height: 1.5rem;
    width: 24px;
    width: 1.5rem;
    margin: 4px 4px;
    margin: .25rem .25rem;
    border-radius: 0 100% 100% 100%;
    transition: background-color 300ms ease-out;
}
nav li.to--pitch a:hover{
    outline: 0;
    transition: background-color 300ms ease-in;
}
nav li.to--pitch a.active{
    outline: 0;
    transition: background-color 300ms ease-in;
}
nav li a{}
nav li a, nav li a:hover, nav li a:visited, nav li a:active{
    outline: 0;
    text-decoration: none;
}
nav li a[href="#section--head"]{}
nav li a[href="#section--head"]:hover, nav li a[href="#section--head"]:focus{
    background-color: rgb(31, 58, 71);
}
nav li a[href="#section--head"].active, nav li a[href="#section--head"]:active{
    background-color: hsl(200,70%,40%);
}
nav li a[href="#section--heart"]{}
nav li a[href="#section--heart"]:hover, nav li a[href="#section--heart"]:focus{
    background-color: rgb(115, 51, 38);
}
nav li a[href="#section--heart"].active, nav li a[href="#section--heart"]:active{
    background-color: hsl(10, 70%,50%);
}
nav li a[href="#section--hands"]{}
nav li a[href="#section--hands"]:hover, nav li a[href="#section--hands"]:focus{
    background-color: rgb(31, 71, 44);
}
nav li a[href="#section--hands"].active, nav li a[href="#section--hands"]:active{
    background-color: hsl(140,70%,40%);
}
nav li a[href="#section--person"]{}
nav li a[href="#section--person"]:hover, nav li a[href="#section--person"]:focus{
    background-color: rgb(115, 102, 38);
}
nav li a[href="#section--person"].active, nav li a[href="#section--person"]:active{
    background-color: hsl(50, 70%,50%);
}
nav li a[href="#section--contact"]{}
nav li a[href="#section--contact"]:hover, nav li a[href="#section--contact"]:focus{
    background-color: rgb(124, 124, 124);
}
nav li a[href="#section--contact"].active, nav li a[href="#section--contact"]:active{
    background-color: hsl(240, 1%, 68%);
}
/* =====================
Pitch
===================== */
#section--pitch{
    display: table;
    width: 100%;
    height: inherit;
    overflow: hidden;
    margin-bottom: 0;
}
#section--pitch .section__content{
    display: table-cell;
    margin: 0;
    padding: 0;
    width: 100%;
    height: inherit;
    font-size: 1.6em;
    vertical-align: middle;
}
#section--pitch .content__row{
    display: inline-block;
    margin-left: .5em;
    margin-right: .5em;
    padding: 2em 0;
    width: auto;
    max-width: 100%;
    text-align: center;
    vertical-align: middle;
    line-height: 1;
}
#section--pitch h1{
    display: none;
}
#section--pitch h2{
    margin: 0 auto;
    max-width: 1660px;
    text-align: center;
    color: #2d2e2e;
    color: rgba(45, 46, 46, 0.75);
    font-weight: 400;
    font-size: 2.5em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-family: 'Oswald',sans-serif;
}
/*transform: skewY(-10deg);*/
#section--pitch h2 span{
    white-space: normal;
}
#section--pitch h2 span strong{
    color: hsl(232,1%,18%);
    font-weight: 700;
}
