    min-height: initial;
}
#section--quote .content__row{
    margin: 0;
}
#section--quote .content__block{
    display: block;
    padding: 0 0 1em 0;
    text-align: center;
    background: none;
    box-shadow: 0 0 0 hsla(0, 0%, 0%, 0);
}
#section--quote blockquote{
    display: inline-block;
    position: relative;
    max-width: 1024px;
    margin: 0 auto;
    padding: 1em 2.5em;
    color: hsl(240, 1%, 60%);
    text-align: justify;
    font-size: 1em;
    font-weight: 400;
}
/* @mixin background-image $quotes-bg-img, left .5em, auto 9em, no-repeat; */
#section--quote blockquote span{
    display: block;
    position: relative;
    text-align: center;
}
#section--quote blockquote span:before, #section--quote blockquote span:after{
    position: absolute;
    top: -0.2em;
    font-family: Georgia, serif;
    font-size: 3.0em;
    font-weight: 400;
    color: hsl(334, 10%, 50%);
}
#section--quote blockquote span:before{
    content: "\201C";
    left: -.5em;
}
#section--quote blockquote span:after{
    content: "\201D";
    right: -.5em;
}
#section--quote blockquote cite{
    display: block;
    margin-top: 5px;
    color: hsl(334, 10%, 50%);
    font-size: .8em;
    text-align: center;
}
#section--quote blockquote cite:before{
    content: "\2014 \2009";
}
#section--quote blockquote cite:after{
    content: "\2009 \2014";
}
#section--quote:after{
    display: none;
}
/* ====================5
Contact
===================== */
#section--contact .section__content{
    padding: 1em 1.25em;
    margin: 0;
    width: auto;
    max-width: 100%;
    min-height: 60vh;
    text-align: center;
    background: url(../img/scene_grey.png);
    background-position: top center;
    background-size: 120% auto;
    background-repeat: no-repeat;
}
#section--contact .content__row{
    margin: 0 auto;
}
#section--contact .content__block{
    padding: .5em 1em;
    background: none;
    border: none;
    box-shadow: none;
    -webkit-transform: skewY(-10deg);
                -ms-transform: skewY(-10deg);
            transform: skewY(-10deg);
}
#section--contact .block--link{
    margin: 1.5em auto;
}
#section--contact a{
    display: block;
    position: relative;
    padding: .5em .4em .5em .4em;
    color: hsl(40,60%,98%);
    text-decoration: none;
    font-weight: 700;
    font-size: 1.75em;
    line-height: 1em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    text-align: center;
    font-family: 'Oswald',sans-serif;
    background: hsl(240, 1%, 68%);
    text-shadow: 0 .1em 0 hsla(0,0%,0%, .3);
    border: 2px solid hsla(0,0%,10%, .7);
    border-bottom-width: .2em;
}
#section--contact a:hover, #section--contact a:focus{
    padding: .5em .4em .5em .4em;
    background: rgb(126, 126, 129);
    border-bottom-color: hsla(0,0%,10%, .7);
}
#section--contact a:active{
    padding: .6em .4em .4em .4em;
    background: hsl(240, 1%, 68%);
    border-color: hsl(232,1%,18%);
    border-top-width: .2em;
    border-bottom-width: 2px;
    text-shadow: 0 -.1em 0 hsla(0,0%,0%, .3);
}
#section--contact a.link--cv{
    background: hsl(140,70%,40%);
}
#section--contact a.link--cv:hover, #section--contact a.link--cv:focus{
    background: rgb(31, 173, 150);
}
#section--contact a.link--cv:active{
    background: hsl(240, 1%, 68%);
}
#section--contact a.link--mail{
    background: hsl(200,70%,40%);
}
#section--contact a.link--mail:hover, #section--contact a.link--mail:focus{
    background: rgb(31, 173, 150);
}
#section--contact a.link--mail:active{
    background: hsl(240, 1%, 68%);
}
/* =====================
Tools
===================== */
#section--tools{
    margin-bottom: 0;
    padding-top: 6em;
    padding-bottom: 3em;
    border-top: 0;

    /*background: linear-gradient(to bottom, color($background-color a(0)) 0%, color($color-black a(.5)) 100%); */
    border-color: transparent;
}
#section--tools .section__content{
    padding: 0 2em 0 2em;
    margin: 0;
    width: 100%;
    max-width: 100%;
    height: auto;
    min-height: 0;
    min-height: initial;
}
#section--tools .section__content:after{
    display: none;
}
#section--tools .content__row{
    margin: 0 auto;
}
#section--tools .content__block{
    padding: 1em;
    background: none;
    box-shadow: none;
    text-align: center;
}
#section--tools .content__head{
    display: block;
    text-align: center;
    color: hsl(232,1%,18%);
}
#section--tools .content__list{
    display: inline-block;
    font-size: 1.2em;
    margin-bottom: 0;
}
#section--tools .content__list li{
    display: inline-block;
    position: relative;
    white-space: nowrap;
    margin: 0 .5em 1em .5em;
}
#section--tools .content__list li a{
    display: block;
    height: inherit;
    width: inherit;
    background: hsla(0, 100%, 100%, 1);
    border-radius: 5px;
    border: 1px solid #fff;
    transition: -webkit-transform .15s ease-in;
    transition: transform .15s ease-in;
    transition: transform .15s ease-in, -webkit-transform .15s ease-in;
}
#section--tools .content__list li a img{
    height: 50px;
    width: auto;
    margin: 10px;
}
#section--tools .content__list li a:hover{
    border: 1px solid rgb(191, 191, 192);
    -webkit-transform: translate(0, -.75em);
                -ms-transform: translate(0, -.75em);
            transform: translate(0, -.75em);
    transition: -webkit-transform .15s ease-in;
    transition: transform .15s ease-in;
    transition: transform .15s ease-in, -webkit-transform .15s ease-in;
}
#section--tools .content__list li:after{
    content: "";
    display: block;
    position: absolute;
    top: 2px;
    bottom: 2px;
    left: 3px;
    right: 3px;
    border-radius: 5px;
    background: hsl(232,1%,18%);
    z-index: -1;
}
#section--tools .content__list li:first-child a:hover{
    -webkit-transform: translate(-.75em, -.75em);
                -ms-transform: translate(-.75em, -.75em);
            transform: translate(-.75em, -.75em);
}
#section--tools .content__list li:last-child a:hover{
    -webkit-transform: translate(.75em, -.75em);
                -ms-transform: translate(.75em, -.75em);
            transform: translate(.75em, -.75em);
}
#section--tools .content__list:last-child li a{}
#section--tools .content__list:last-child li a:hover{
    -webkit-transform: translate(0, .75em);
                -ms-transform: translate(0, .75em);
            transform: translate(0, .75em);
    transition: -webkit-transform .15s ease-in;
    transition: transform .15s ease-in;
    transition: transform .15s ease-in, -webkit-transform .15s ease-in;
}
#section--tools .content__list:last-child li:first-child a:hover{
    -webkit-transform: translate(-.75em, .75em);
                -ms-transform: translate(-.75em, .75em);
            transform: translate(-.75em, .75em);
}
#section--tools .content__list:last-child li:last-child a:hover{
    -webkit-transform: translate(.75em, .75em);
                -ms-transform: translate(.75em, .75em);
            transform: translate(.75em, .75em);
}
#section--tools:after{
    display: none;
}
#section--tools.section--active{
    border-color: transparent;
}
#section--tools.section--active .content__block{
    border: none;
}
/* =====================
Footer
===================== */
footer{
    display: block;
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding: 1em 0;
    background: hsl(232,1%,18%);

    /* $section-active-bg-color; */
}
.footer__content{
    padding: 0;
    max-width: 1280px;
    text-align: center;
}
.footer__content .content__text{
    display: block;
    margin: 0;
    padding: .5em 0;
    font-size: .8em;
    color: rgb(150, 156, 154);
}
.footer__content .content__text--links{
    margin-bottom: 2.5em;
}
.footer__content .link{
    display: inline-block;
    padding: .5em .75em;
    border-radius: .25em;
    margin: 0 .5em .5em .5em;
}
.footer__content .link:hover{
    background: rgb(150, 156, 154);
}
.footer__content .link:hover .fa{
    color: hsl(232,1%,18%);
