    vertical-align: middle;
    font-size: 1.4em;
}
.footer__content .link .fa{
    color: rgb(150, 156, 154);
    vertical-align: top;
    margin-right: .25em;
}
.footer__content .link span{
    display: inline-block;
    font-family: 'Oswald',sans-serif;
}

